import { useEffect, useState } from "react";
import "./styles.css";

export default App = ({ value }) => {
  const [time, setTime] = useState(Date().toString());

  let showTime = false;
  let showArray = false;

  if (!value) {
    showTime = true;
  }

  if (Array.isArray(value)) {
    showArray = true;
  }

  useEffect(() => {
    if (showTime) {
      setInterval(() => {
        setTime(Date().toString());
      }, 1000);
    }
  }, [showTime, time]);

  if (showArray)
    return (
      <ul>
        {value.length > 0 && value.map((elem) => <div>{elem.toString()}</div>)}
      </ul>
    );

  if (showTime) {
    return (
      <>
        <div class="date"> Time: {time.substring(16, 33)} </div>
        <div class="date"> Day: {time.substring(0, 15)}</div>
      </>
    );
  }

  return <div>{value.toString()}</div>;
};
